import React from 'react';
import RandomUtil from './../utils/RandomUtil';

class Rain {
    maxLength = 10;
    speed = RandomUtil.randomFromTo(3, 8);

    constructor(screen, isFirstLoad) {
        this.directionX = 0;
        
        this.x = RandomUtil.randomUntil(screen.width);
        this.y = isFirstLoad || false ? RandomUtil.randomUntil(screen.height / 2) : 0;
        this.length = RandomUtil.randomFromTo(1, this.maxLength);
        this.directionX = RandomUtil.randomFromTo(-1, 1, true) / 2;
        this.directionY = 1;
    }

    update = () => {
        this.x += this.directionX * this.speed;
        this.y += this.directionY * this.speed;
    }

    draw = (context) => {
        let directionTo = {
            x: this.x + (this.directionX * this.length),
            y: this.y + (this.directionY * this.length)
        };

        context.beginPath();
        context.moveTo(this.x ,this.y);
        context.lineTo(directionTo.x, directionTo.y);
        context.stroke();

        this.update();
    }

    isCollide = (screen) => {
        let obstacle = document.getElementsByClassName('obstacle')[0];
        if(typeof(obstacle) !== 'undefined' && obstacle.children.length > 0) {
            if(this.x + (this.directionX * this.speed) >= obstacle.offsetLeft 
                && this.x + (this.directionX * this.speed) <= obstacle.offsetLeft + obstacle.offsetWidth
                && this.y + (this.directionY * this.speed) >= obstacle.offsetTop
                && this.y + (this.directionY * this.speed) <= obstacle.offsetTop + obstacle.offsetHeight) {
                return true;
            }
        }

        return this.x < 0 || this.x > screen.width || this.y > screen.height;
    }
}

class RainCanvas extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            screen: {
                width: 0,
                height: 0
            },
            rains: [],
            rainMax: 50
        }
    }

    getWindowSize = () => {
        let { screen, rainMax } = this.state;

        screen.width = window.innerWidth;
        screen.height = window.innerHeight;
        rainMax = Math.floor(screen.width * 0.5);

        this.setState({
            screen,
            rainMax
        })
    }

    initializeRain = () => {
        const { rainMax, screen } = this.state;
        let { rains } = this.state;
        for(let i=0;i<rainMax;i++) {
            rains.push(new Rain(screen, true));
        }

        this.setState({
            rains
        });
    }

    animate = () => {
        const { screen } = this.state;
        let canvas = document.getElementsByClassName('rain-canvas')[0];
        let context = canvas.getContext('2d');
        context.strokeStyle = '#FFFFFF';
        context.clearRect(0, 0, screen.width, screen.height);
        let { rains } = this.state;
        
        if(rains.length === 0) this.initializeRain();

        rains.forEach((r, i) => {
            if(r.isCollide(screen)) {
                rains.splice(i, 1);
                rains.push(new Rain(screen));
                this.setState({
                    rains
                });
            }
            r.draw(context);
        });
    }

    componentDidMount = () => {
        window.addEventListener('resize', this.getWindowSize);
        this.getWindowSize();
        setInterval(this.animate, 1000 / 120);
    }

    render = () => {
        const { screen } = this.state;
        const { component } = this.props;

        return (
            <div className="flex-center">
                <canvas width={screen.width} height={screen.height} className="rain-canvas"></canvas>
                <div className="obstacle">
                    { component }
                </div>
            </div>
        );
    }

}

export default RainCanvas;