import React from 'react';
import styled from 'styled-components';
import RandomUtil from './../utils/RandomUtil';

class Typing extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            strings: props.stringCollections || [],
            falseStrings: props.falseStringCollections || [],
            stringIndex: -1,
            shownString: ' ',
            falseCount: 0,
            maxFalseCount: 2,
            isComplete: false
        };
        this.interval = props.interval || 1500;
    }

    setString = (then) => {
        let { stringIndex, strings } = this.state;
        ++stringIndex;
        stringIndex %= strings.length;
        this.setState({
            stringIndex,
            shownString: ''
        }, then);
    }

    typeString = () => {
        let { shownString, stringIndex, strings, falseStrings, falseCount, maxFalseCount } = this.state;
        let string = strings[stringIndex]
        let newChar = string[shownString.length];

        let isFalse = RandomUtil.randomUntil(2) === 0 && falseCount !== maxFalseCount && shownString.length !== string.length - 1;
        if(isFalse) {
            let falseString = falseStrings[stringIndex];
            let idx = RandomUtil.randomUntil(falseString.length - 1);
            newChar = falseString[idx];
            ++falseCount;
        }
        shownString += newChar;

        this.setState({
            shownString,
            isComplete: shownString === string,
            falseCount
        }, () => {
            const { shownString, strings, stringIndex } = this.state;
            if(isFalse) {
                setTimeout(() => {
                    this.setState({
                        shownString: shownString.substr(0, shownString.length - 1),
                        isComplete: false
                    }, () => {
                        setTimeout(this.typeString, this.interval / 2);
                    });
                }, this.interval / 2);
            }
            else {
                if(shownString === strings[stringIndex]) {
                    setTimeout(() => {
                        this.setState({
                            shownString: '',
                            isComplete: false,
                            falseCount: 0
                        }, () => {
                            this.setString(() => {
                                setTimeout(this.typeString, this.interval);
                            });
                        });
                    }, this.interval * 1.5);
                }
                else {
                    this.setState({
                        falseCount: 0
                    }, () => {
                        setTimeout(this.typeString, this.interval);
                    });
                }
            }
        });
    }

    componentDidMount = () => {
        const { strings } = this.state;
        if(typeof(strings) === typeof([]) && strings.length > 0) {
            this.setString(() => {
                this.typeString();
            });
        }
    }

    render = () => {
        const { shownString, isComplete } = this.state;
        const { fontSize, textAlign } = this.props;

        const Greetings = styled.div`
            text-transform: capitalize;
            font-size: ${fontSize || 16}px;
            text-align: ${textAlign || 'left'};
            width: 100%;
        `;

        return (
            <Greetings><span className={"greeting-text text-cursor" + (isComplete ? ' highlighted' : '')}>{ shownString }</span>&zwnj;</Greetings>
        );
    }

}

export default Typing;