class RandomUtil {

    /**
     * 
     * @param Number max 
     * @param boolean isDecimal
     * 
     * @return If isDecimal true, then return decimal random number until max (exclusive). Otherwise, return integer.
     */
    static randomUntil = (max, isDecimal = false) => {
        let result = Math.random() * max;
        return isDecimal ? result : Math.floor(Math.random() * max);
    }

    /**
     * 
     * @param Number from 
     * @param Number to 
     * @param boolean isDecimal 
     * 
     * @return If isDecimal true, then return decimal random number from param from until param to (exclusive). Otherwise, return integer.
     */
    static randomFromTo = (from, to, isDecimal = false) => {
        let result = Math.random() * (to - from) + from;
        return isDecimal ? result : Math.floor(result);
    }

}

export default RandomUtil;