import React from 'react';
import styled from 'styled-components';
import RainCanvas from './../components/RainCanvas';
import { routes } from './../routes';

class MainPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            menu: 'about me'
        }
    }

    getComponent = () => {
        let { menu } = this.state;
        if(!Object.keys(routes).includes(menu)) {
            menu = 'home';
            this.setState({
                menu: 'home'
            });
        }

        return routes[menu].component;
    }

    changeMenu = (menu) => {
        this.setState({
            menu
        });
    }

    Header = () => {
        const Header = styled.div `
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            justify-content: center;
            padding: 20px;
            width: 100vw;
            color: white;
        `;

        const { menu } = this.state;
        let menus = Object.keys(routes);

        return (
            <Header>
                {
                    menus.map((m, i) => (
                        <p key={i} className={`menu ${(menu === m ? 'menu-active' : '')}`} 
                            onClick={ () => this.changeMenu(m) }>
                            { m }
                        </p>
                    ))
                }
            </Header>
        );
    }

    Footer = () => {
        const Footer = styled.div `
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: center;
            padding: 20px;
            width: 100vw;
            color: black;
        `;

        return (
            <Footer>
                &copy; Andy Lie
            </Footer>
        );
    }

    ShowComponent = () => {
        const Box = styled.div `
            border: 1px solid black;
            padding: 5px 10px;
            box-shadow: 5px 5px 10px #34495e;
            width: 75vw;
            height: 50vh;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        `;

        return (
            <>
                { this.Header() }
                <Box>
                    { this.getComponent() }
                </Box>
                { this.Footer() }
            </>
        );
    }

    render = () => {
        let show = this.ShowComponent();

        return (
            <>
                <RainCanvas component={ show } />
            </>
        );
    }
}

export default MainPage;