import React from 'react';
import { greetings } from './../constants';
import Typing from './../components/Typing';
import styled from 'styled-components';

class IntroductionPage extends React.Component {

    render = () => {
        const trueGreetings = greetings.map(g => g.true);
        const falseGreetings = greetings.map(g => {
            g.false = g.false.split('').filter((f, pos, self) => {
                return self.indexOf(f) === pos && g.true.indexOf(f) === -1;
            }).join('');
            return g.false;
        });

        const Introduction = styled.p `
            font-size: 20px;
        `;

        const { name } = this.props;

        return (
            <>
                <Typing stringCollections={ trueGreetings }
                        falseStringCollections={ falseGreetings }
                        fontSize={30}
                        textAlign="center"
                        interval={1000} />
                <Introduction>My name is { name || 'Andy Lie' }</Introduction>
            </>
        );
    }
}

export default IntroductionPage;