import React from 'react';
import styled from 'styled-components';

class AboutMePage extends React.Component {
    render = () => {
        const Description = styled.div `
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            padding: 0 2%;
            font-size: 18px;
            text-align: left;
            line-height: 1.5;
            background: url('/logo172.png');
            background-size: 200px 261px;
            background-repeat: no-repeat;
            background-position: 90% 100%;
            overflow-y: auto;
        `;

        const DescriptionLine = styled.p `
            margin-top: 10px;
        `;

        const DescriptionSignature = styled.div `
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            padding-top: 20px;
        `;

        const LittleSignature = styled.p `
            font-size: 14px;
        `;

        return (
            <>
                <h1 className="box-title">About Me</h1>
                <Description>
                    <DescriptionLine>
                        Hi, my name is Andy and Lie is my family name. Because my name is only 4 letters and I think it's pretty short so I add my family name by myself. Well, I have a dream when I was 10 years old. "<strong>I want change my homeland, my country and world by technology</strong>" that was my dream before. After I grew up, I knew what was my dream about. Programmer is my dream since I was 10 years old. Now my problem is where I can go there?
                    </DescriptionLine>
                    <DescriptionLine>
                        It started when I was 15 years old when I was teached by <strong>Mr. Haryandoko</strong> on my Senior High School. He teached me about how to code in C programming language and teached me about logical thinking. Then after graduate from Senior High School. I went to <strong>BINUS University</strong> and assigned myself to <strong>Software Laboratory Center</strong> (<strong>SLC</strong>).
                    </DescriptionLine>
                    <DescriptionLine>
                        Honestly, SLC was my little step to take a bigger step in a future. I learned more and more when I was a Teaching Assistant and now I am still learning more as Research and Development Staff. It was a good memories I've been through. I met new family in my life, new environment and new experience. After 3 years I stayed here and now <strong>it is time to continue my journey</strong>.
                    </DescriptionLine>
                    <DescriptionSignature>
                        <p>I am</p>
                        <strong>AY of Bluejacket 17-2</strong>
                        <LittleSignature>~<em> Achieve Success Through Faith, Effort, and Teamwork | Perfection Comes From Positive Attitude </em>~</LittleSignature>
                    </DescriptionSignature>
                </Description>
            </>
        );
    }
}

export default AboutMePage;