import React from 'react';
import styled, { keyframes } from 'styled-components';

class Chart extends React.Component {

    getColorCode = (number) => {
        const colorCodes = {
            '0% - 25%': '#ff5252', // red
            '26% - 50%': '#ff793f', // orange
            '51% - 75%': '#ffb142', // yellow
            '76% - 100%': '#33d9b2', // green
        };

        let colorCode = '';
        Object.keys(colorCodes).forEach((cc) => {
            let maxMin = cc.split(' - ').join('').split('%').filter((c) => c !== '');
            let min = parseInt(maxMin[0]);
            let max = parseInt(maxMin[1]);

            if(number >= min && number <= max) {
                colorCode = colorCodes[cc.trim()];
                return;
            } 
        });

        return colorCode;
    }

    ChartBar = (record, width) => {
        const { name, percentage } = record;

        let kfSlideUp = '';
        for(let i = 0;i<100;i++) {
            kfSlideUp += `${i}% { max-height: ${i}%; }`;
        }
        const SlideUp = keyframes `${kfSlideUp}`;

        const Bar = styled.div `
            width: ${width}%;
            height: ${percentage}%;
            background-color: ${this.getColorCode(percentage)};
            position: relative;
            display: flex;
            justify-content: center;
            animation: ${SlideUp} 1.5s linear;
        `;

        const BarName = styled.div `
            position: absolute;
            bottom: 100%;
        `;

        return (
            <Bar key={name + percentage}>
                <BarName>
                    <p>{name}</p>
                    <p>{percentage}%</p>
                </BarName>
            </Bar>
        );
    }

    render = () => {
        let data = this.props.data || [];
        // data = data.sort((d1, d2) => d1.percentage < d2.percentage ? 1 : -1);
        data = data.sort((d1, d2) => d1.name > d2.name ? 1 : -1);

        const maxWidth = 90;
        const width = maxWidth / data.length;

        const ChartWrapper = styled.div `
            width: 100%;
            height: 100%;
            padding: 1%;
            display: flex;
        `;

        const ChartHelper = styled.div `
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-end;
            height: 100%;
            width: fit-content;
        `;

        const ChartDisplay = styled.div `
            margin-left: 5px;
            border-left: 1px solid white;
            border-bottom: 1px solid white;
            width: 100%;
            display: flex;
            justify-content: space-around;
            align-items: flex-end;
        `;

        return (
            <>
                <ChartWrapper>
                    <ChartHelper>
                        <p>100%</p>
                        <p>0%</p>
                    </ChartHelper>
                    <ChartDisplay>
                        {
                            data.map((d) => this.ChartBar(d, width))
                        }
                    </ChartDisplay>
                </ChartWrapper>
            </>
        );
    }
}

export default Chart;